<template lang="pug">
.upload
  //- 
  .position-relative.h-100(v-if='hasUpload')
    img.img-preview.w-100.h-100(:src='img.url' alt='upload file')
    .img-delete-icon.iconfont.icon-times-circle(@click='deleteImg')
  //- 
  SelectFile.h-100(v-else
    :class='{ "invalid": isInvalid }'
    :isUploading='isUploading'
    acceptFileType='.png, .jpg, .jpeg'
    :tipText='tipText'
    @set-upload-file="validateFile")
    .size-tip.mt-2.text-center(v-show="isSizeTipShown")
      | {{ 'Image Size' + maxSize + 'kb' }}
      br
      | {{ 'Pixel Size' + maxPixelSize + '*' + maxPixelSize }}
</template>

<script>
import SelectFile from './SelectFile.vue'

export default {
  name: 'img-upload',
  props: {
    img: { // img{ url, id }
      type: Object,
      required: true
    },
    maxSize: { // 500kb
      type: Number,
      default: 500
    },
    maxPixelSize: { // 800*800
      type: Number,
      default: 800
    },
    tipText: String, // 
    isInvalid: { // 
      type: Boolean,
      default: false
    },
    uploadUrl: { // url
      type: String,
      default: '/seller/image/upload'
    },
    isSizeTipShown: { // 
      type: Boolean,
      default: true
    },
    isUploading: { // 
      type: Boolean,
      default: false
    }
  },
  components: {
    SelectFile
  },
  computed: {
    hasUpload() { // 
      return this.img.url !== ''
    }
  },
  methods: {
    /**
     * 
     * @param {object} file - 
     */
    validateFile(file) {
      // 
      if (file.size > this.maxSize * 1024) { // size
        this.$emit('size-err')
        return
      }
      // 
      let reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = (e) => {
        // 
        let img = new Image()
        img.src = e.target.result
        img.onload = () => {
          if (img.width > this.maxPixelSize || img.height > this.maxPixelSize) { // 
            this.$emit('size-err')
          } else {
            this.upload(file)
          }
        }
      }
    },
    upload(file) { // 
      this.$emit('img-upload', file)
    },
    deleteImg() { // 
      this.$emit('img-delete')
    },
  }
}
</script>

<style scoped lang="scss">
.upload {
  width: 10rem;
  height: 10rem;
  .img-preview {
    border-radius: .25rem;
  }
  .invalid {
    background-color: #FCDCD8;
  }
}

.size-tip {
  font-size: 0.875rem;
  line-height: 1rem;
  color: #999;
}
</style>