<template lang="pug">
div(:class='{ "modal-open": isShown }')
  .modal.fade(:class='modalClass')
    .modal-dialog.modal-dialog-centered(:class='[modalType]')
      .modal-content
        .modal-header
          h5.modal-title {{ title }}
          button.close(type='button' @click='hide')
            span &times;
        .modal-body
          p {{ message }}
        .modal-footer
          button.btn.btn-primary(type='button', @click='hide') {{ confirmText }}
  .modal-backdrop.fade(v-show='isShown' :class='{ "show": isShown }')
</template>

<script>
import addBodyOverflow from './utils'

export default {
  name: 'cf-alert-box',
  props: {
    modalType: { // modal .modal-sm .modal-md .modal-lg 
      type: String,
      default: 'modal-sm'
    },
    confirmText: { // 
      type: String,
      default: ''
    },
  },
  data () {
    return {
      isShown: false, // 
      title: '', // 
      message: '' // 
    }
  },
  computed: {
    modalClass () {
      return {
        'show': this.isShown,
        'd-block': this.isShown
      }
    }
  },
  methods: {
    hide () {
      this.isShown = false
      addBodyOverflow(false)
    },
    /**
     * 
     * @param {string} title - 
     * @param {string} msg - 
     */
    show (title, msg) {
      this.isShown = true
      this.title = title
      this.message = msg
      addBodyOverflow(true)
    }
  }
}
</script>
