import emitter from '../mixins/emitter'

function getEleIndexOfParent(ele, parentEle) { // 
  return Array.prototype.indexOf.call(parentEle.childNodes, ele)
}
function getDraggableEle(event, rootEle) { // 
  let target = event.target
  while (target && target.tagName !== 'BODY'){
    if (getEleIndexOfParent(target, rootEle) !== -1) {
      return target
    }
    target = target.parentNode
  }
  return null
}

const draggable = {
  name: 'draggable',
  componentName: 'draggable',
  mixins: [emitter],
  props: {
    list: { // 
      type: Array,
      required: true
    },
    tag: { // 
      type: String,
      default: 'div'
    },
    draggableFlag: { // 
      type: Boolean,
      default: true
    },
    hasHandle: { // 
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      transitionMode: false, // transition
      draggingIndex: -1, // 
      dragHandleFlag: false //  drag handle 
    }
  },
  computed: {
    rootElement () { // 
      return this.transitionMode ? this.$el.childNodes[0] : this.$el
    },
    draggable () {
      if (this.hasHandle) {
        return this.draggableFlag && this.dragHandleFlag
      } else {
        return this.draggableFlag
      }
    }
  },
  watch: {
    draggable (cur) {
      this.broadcast('draggable-item', 'changed-draggable-flag', cur)
    }
  },
  render (h) {
    let defaultSlot = this.$slots.default
    //  transition-group
    if (defaultSlot && defaultSlot.length === 1) {
      let childNode = defaultSlot[0]
      if(childNode.componentOptions && childNode.componentOptions.tag === 'transition-group') {
        this.transitionMode = true
      }
    }
    return h(this.tag, defaultSlot)
  },
  methods: {
    dragStart ($event) { // 
      let ele = getDraggableEle($event, this.rootElement)
      this.draggingIndex = getEleIndexOfParent(ele, this.rootElement)
    },
    moveDraggableItem ($event) { // 
      let targetEle = getDraggableEle($event, this.rootElement)
      let targetIndex = getEleIndexOfParent(targetEle, this.rootElement)
      let list = this.list
      let tmp = list.splice(this.draggingIndex, 1)
      list.splice(targetIndex, 0, tmp[0])
    },
    changeDragHandleFlag (data) {
      this.dragHandleFlag = data
    }
  },
  mounted () {
    let supportDraggable = ('draggable' in document.createElement('div'))
    if (!supportDraggable) { // 
      console.error('h5\nh5https://caniuse.com/#feat=dragndrop')
      return
    }
    this.broadcast('draggable-item', 'changed-draggable-flag', this.draggable)
  },
  created () {
    this.$on('draggable-item-start', this.dragStart)
    this.$on('draggable-item-drop', this.moveDraggableItem)
    this.$on('changed-drag-handle-flag', this.changeDragHandleFlag)
  }
}

export default draggable