<template lang="pug">
ul.nav.nav-tabs
  li.nav-item(v-for='(tab, index) in tabs')
    a.nav-link.mb-0.text-capitalize(:class='{ "active": isActive(index) }' href='#'
      @click.prevent='setActive(index)'
    ) {{ tab.text }}
    span.highlight
</template>

<script>
export default {
  name: 'cf-nav-tabs',
  props: {
    tabs: { // tab - [{text,...}]
      type: Array,
      required: true
    },
    activeIndex: { //  active tab
      type: Number,
      default: 0
    }
  },
  methods: {
    /**
     * tabactive
     * @param {number} index - tab
     */
    isActive (index) {
      return index === this.activeIndex
    },
    /**
     * tabactivetab
     * @param {number} to - tab
     */
    setActive (to) {
      this.$emit('update:activeIndex', to)
    }
  }
}
</script>
