<template lang="pug">
.tooltip-wrapper.position-relative
  slot
    i.fa.fa-question-circle
  .tooltip(:class='tooltipClass')
    .arrow
    .tooltip-inner(:style='{"max-width": maxWidth}') {{ content }}
</template>

<script>
export default {
  name: 'tooltip',
  props: {
    direction: { // tooltip/topleftrightbottom
      type: String,
      default: 'top'
    },
    content: { // 
      type: String,
      required: true
    },
    maxWidth: { // 
      type: String,
      default: '20rem'
    }
  },
  computed: {
    tooltipClass () { //  direction  tooltip 
      let classArr = this.direction.split('-')
      classArr[0] = 'bs-tooltip-' + classArr[0]
      return classArr.join(' ')
    }
  }
}
</script>

<style scoped lang="scss">
.tooltip {
  display: none;
  /* tooltip  tooltip-inner  max-width */
  width: 20rem;
  line-height: .875rem;
  .tooltip-inner {
    text-align: left;
    box-shadow: 0 2px 4px 0 rgba(226, 231, 232, 0.5);
  }
}
/**/
%tooltip-horizontal-position {
  top: 50%;
  .arrow {
    top: 50%;
    transform: translateY(-50%);
  }
}
/**/
%tooltip-vertical-position {
  left: 50%;
  display: flex;
  justify-content: center;
}

/*hover*/
.tooltip-wrapper:hover > .tooltip {
  opacity: 0.8;
  display: block;
  &.bs-tooltip-left {
    @extend %tooltip-horizontal-position;
    transform: translate(-100%, -50%);
    .tooltip-inner {
      float: right;
    }
  }
  &.bs-tooltip-right {
    @extend %tooltip-horizontal-position;
    left: 100%;
    transform: translateY(-50%);
    .tooltip-inner {
      float: left;
    }
  }
  &.bs-tooltip-top {
    @extend %tooltip-vertical-position;
    top: 0;
    transform: translate(-50%, -100%);
  }
  &.bs-tooltip-bottom {
    @extend %tooltip-vertical-position;
    transform: translateX(-50%);
  }
}
</style>
